html,body{margin:0;padding:0}html{height:100%}body{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;height:100%}.container{left:0;position:absolute;width:100%;height:100%;background:#e3e3e3;overflow:hidden}.dragger{width:100%;height:100%;background:#aaa;display:block;cursor:move}.dragger .head{text-align:center;height:50px;width:100%;background:teal;display:block;color:white}.dragger .head h2{margin:0;padding:10px 8px}.content{box-sizing:border-box;position:absolute;width:100%;background:white;top:50px;overflow:hidden;padding:10px}



